import { useState, useEffect } from "react";
import css from './DeliveryRequest.module.css';

export const DeliveryRequest = ({ onClose, nameDelivery }) => {

    const [name, setName] = useState('');
    const [telefon, setTelefon] = useState('');
    const [email, setEmail] = useState('');
    //const [service, setService] = useState('');

    const handleChange = ({ target: { name, value } }) => {
        switch (name) {
            case 'name':
                setName(value);
                break;
            case 'number':
                setTelefon(value);
                break;
            case 'email':
                setEmail(value);
                break;
            //case 'service':
            //    setService(value);
            //    break
            default:
        }
    };

    const handlSubmit = (event) => {
        event.preventDefault();
        setName("");
        setTelefon("");
        setEmail("");
        //setService("");
    };

    useEffect(() => {
                            //          
        const handleClickOutside = (event) => {
            onClose()
        };
        document.addEventListener('click', handleClickOutside);
                            //      
        return () => {
            document.removeEventListener('click', handleClickOutside);
        };
    }, [onClose]);

    return (
        <div className={css.request}>
            <h3> </h3>
            <p>
                 ,   /         .
            </p>
            <form className={css.addForm} onSubmit={handlSubmit}>
                <label className={css.label}>
                    <p>:</p>
                    <input
                        name="name" type="text" value={name} onChange={handleChange}
                        pattern="^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$"
                        placeholder="  *"
                        title=""
                        required
                    />
                </label>
                <label className={css.label}>
                    <p> :</p>
                    <input
                        name="telefon" type="tel" value={telefon} onChange={handleChange}
                        pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
                        placeholder="+7 (___) ___ - __ - __ *"
                        title="Phone number must be digits and can contain spaces, dashes, parentheses and can start with +"
                        required
                    />
                </label>
                <label className={css.label}>
                    <p>Email:</p>
                    <input
                        name="email" type="email" value={email} onChange={handleChange}
                        pattern="^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$"
                        placeholder=" e-mail *"
                        title="Email"
                        required
                    />
                </label>
                <label className={css.label}>
                    <p>  :</p>
                    <input
                        name="service" type="text" value={nameDelivery} onChange={handleChange}
                        pattern="^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$"
                        placeholder=" "
                        title="    "
                    />
                </label>
                <label className={css.label}>
                    <p>:</p>
                    <textarea
                        name="note" onChange={handleChange}
                        pattern="^[a-zA-Z--]+(([' -][a-zA-Z-- ])?[a-zA-Z--]*)*$"
                        placeholder="   /,    ,         *"
                        title=""
                        required>
                        
                    </textarea>
                </label>
                <button className={css.btn} type="submit"></button>
            </form>
            <p>* -  </p>
        </div>
    )
};